/*
 * 处理页面的的异常或loadding状态
 */

import { useCallback, useReducer, useState } from "react";
import { useMountedRef } from "util/index";

interface State<D> {
  error: Error | null;
  data: D | null;
  stat: "idle" | "loading" | "error" | "success";
}

// 定义初始值
const defaultInitialState: State<null> = {
  stat: "idle",
  data: null,
  error: null,
};

const defaultConfig = {
  throwOnError: false,
};

const useSafeDispatch = <T>(dispatch: (...args: T[]) => void) => {
  const mountedRef = useMountedRef();
  return useCallback(
    (...args: T[]) => (mountedRef.current ? dispatch(...args) : void 0), // void等同于undefined
    [dispatch, mountedRef]
  );
};

export const useAsync = <D>(
  initialState?: State<D>,
  initialConfig?: typeof defaultConfig
) => {
  const config = { ...defaultConfig, ...initialConfig };
  // 如果用户传入了参数是会覆盖掉默认值的
  const [state, dispatch] = useReducer(
    (state: State<D>, action: Partial<State<D>>) => ({ ...state, ...action }), // 这个dispatch相等于把这两个对象扩展
    {
      ...defaultInitialState, // 初始值
      ...initialConfig, // 可有可无
    }
  );

  const safeDispatch = useSafeDispatch(dispatch);

  // 当retry并调用的时候重新调用一次run 也就是刷新一次用户列表
  // useState有一个惰性加载 如果我们传入的直接是一个函数，那么这个组件初始化时一定会执行这个函数
  // 所以这个多套一层，让他初始化时还是一个空函数
  const [retry, setRetry] = useState(() => () => {});

  const setData = useCallback(
    (data: D) =>
      safeDispatch({
        data,
        stat: "success",
        error: null,
      }),
    [safeDispatch]
  );

  const setError = useCallback(
    (error: Error) =>
      safeDispatch({
        error,
        data: null,
        stat: "error",
      }),
    [safeDispatch]
  );

  // 处理请求
  // runConfig用于判断当前请求需不需进行再次请求，也就是刷新
  const run = useCallback(
    (promise: Promise<D>, runConfig?: { retry: () => Promise<D> }) => {
      if (!promise || !promise.then) {
        throw new Error("请传入 promise 类型");
      }

      setRetry(() => () => {
        if (runConfig?.retry) {
          run(runConfig?.retry(), runConfig);
        }
      });

      safeDispatch({ stat: "loading" });

      return promise
        .then((data) => {
          setData(data);
          return data;
        })
        .catch((error) => {
          // catch会主动的消化异常，如果这里不抛出，则在外面就调用这个方法出现异常时，是不会返回出去的
          setError(error);
          if (config.throwOnError) return Promise.reject(error);
          return error;
        });
    },
    [config.throwOnError, safeDispatch, setData, setError]
  );

  return {
    isIdle: state.stat === "idle",
    isLoading: state.stat === "loading",
    isError: state.stat === "error",
    isSuccess: state.stat === "success",
    run,
    setData,
    setError,
    retry,
    ...state,
  };
};
