/*
 * 注册全局路由
 */

import React from "react";
import { Navigate, useRoutes } from "react-router-dom";
import { EpicScreen } from "screens/Epic";
import Kanban from "screens/Kanban";
import ProjectList from "screens/project-list";
import ProjectSreen from "screens/ProjectSreen";
import { PAGE } from "./route-cantan";

const routes = [
  {
    path: PAGE.ROOT,
    element: <Navigate to={PAGE.PROJECTS} />,
  },
  {
    path: PAGE.PROJECTS,
    element: <ProjectList />,
  },
  {
    path: PAGE.PROJECTS + "/:id",
    element: <ProjectSreen />,
    children: [
      {
        path: PAGE.EPIC,
        element: <EpicScreen />,
      },
      {
        path: PAGE.KANBAN,
        element: <Kanban />,
      },
      {
        path: "",
        element: <Navigate to={PAGE.KANBAN} replace />,
      },
      // {
      //   path: PAGE.ROOT,
      //   element: <Navigate to={PAGE.KANBAN} />
      // }
    ],
  },
];

export const CreateRoute = () => {
  return <>{useRoutes(routes)}</>;
};
