import React, { ReactNode, useState } from "react";
import { User } from "type/User";
import { useMount } from "../util";
import { http } from "util/http";
import * as auth from "../auth-provider";
import { useAsync } from "util/use-async";
import { FullPageErrorFallback, FullPageLoading } from "components/lib";
import { useQueryClient } from "react-query";

/*
 *  定义全局是否登录状态
 */

interface AuthForm {
  username: string;
  password: string;
}

// 初始化状态
const bootstrapUser = async () => {
  let user = null;
  const token = auth.getToken();
  if (token) {
    const data = await http("me", { token });
    user = data.user;
  }
  return user;
};

// 创建一个context
const AuthContext = React.createContext<
  | {
      user: User | null;
      register: (from: AuthForm) => Promise<void>;
      login: (from: AuthForm) => Promise<void>;
      logout: () => Promise<void>;
    }
  | undefined
>(undefined);

// displayName用于做标签文本
AuthContext.displayName = "AuthContext";

export const AuthProvider = ({ children }: { children: ReactNode }) => {
  // 用来保存当我们登录或注册或删除时返回的信息
  const {
    data: user,
    error,
    isLoading,
    isIdle,
    isError,
    run,
    setData: setUser,
  } = useAsync<User | null>();

  const queryClicent = useQueryClient();

  const login = (from: AuthForm) =>
    auth.login(from).then((user) => setUser(user));
  const register = (from: AuthForm) =>
    auth.register(from).then((user) => setUser(user));
  const logout = () =>
    auth.logout().then(() => {
      setUser(null);
      queryClicent.clear();
    });

  useMount(() => {
    run(bootstrapUser());
  });

  if (isIdle || isLoading) {
    return <FullPageLoading />;
  }

  if (isError) {
    return <FullPageErrorFallback error={error} />;
  }

  // 当上面两个if中有一个执行时，下面的语句都不会被执行到

  return (
    <AuthContext.Provider
      children={children}
      value={{ user, login, register, logout }}
    />
  );
};

// 检测使用这个 hooks的组件是否在Context上下文中，不在不能获取到传递的属性和方法
export const useAuth = () => {
  const context = React.useContext(AuthContext);
  if (!context) {
    throw new Error("useAuth必须在AuthProvider内使用");
  }
  return context;
};
