import { Rate } from "antd";
import React from "react";

interface PinPorps extends React.ComponentProps<typeof Rate> {
  checked: boolean;
  onCheckedChange?: (checked: boolean) => void;
}

export default function Pin({
  checked,
  onCheckedChange,
  ...restProps
}: PinPorps) {
  return (
    <Rate
      count={1}
      value={checked ? 1 : 0}
      onChange={(num) => onCheckedChange?.(!!num)}
      {...restProps}
    />
  );
}
